* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: url(../img/bg-intro-mobile.png), linear-gradient(hsl(0deg, 100%, 74%) 0%, hsl(0deg, 100%, 74%) 100%);
  padding-block: 50px;
  padding-inline: 30px;
  text-align: center;
  color: #fff;
  font-family: "Poppins", sans-serif;
}
@media (min-width: 768px) {
  body {
    background-image: url(../img/bg-intro-desktop.png), linear-gradient(hsl(0deg, 100%, 74%) 0%, hsl(0deg, 100%, 74%) 100%);
  }
}

@media (min-width: 768px) {
  .main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
    gap: 20px;
    max-width: 75vw;
  }
}

.denied {
  cursor: not-allowed;
  opacity: 0.5;
}

.accepted {
  cursor: pointer;
  opacity: 1;
}

@media (min-width: 768px) {
  .description {
    margin-top: 60px;
    text-align: left;
    padding: 30px;
    width: 50%;
  }
}
.description__heading {
  font-size: 20px;
  max-width: 60%;
  margin: auto;
  letter-spacing: 1px;
}
@media (min-width: 768px) {
  .description__heading {
    font-size: 40px;
    max-width: 100%;
    line-height: 1.2;
    margin: 0;
  }
}
.description__text {
  margin: 20px auto;
  font-size: 12px;
  line-height: 1.5;
  max-width: 75%;
}
@media (min-width: 768px) {
  .description__text {
    margin: 10px 20;
    font-size: 14px;
    line-height: 1.5;
    max-width: 100%;
  }
}

.content {
  max-width: 450px;
  margin: 0 auto;
}

.offer {
  color: hsl(246deg, 25%, 77%);
  margin-top: 40px;
  background-color: hsl(248deg, 32%, 49%);
  padding: 10px 5px;
  font-size: 12px;
  border-radius: 10px;
  box-shadow: 0 8px 2px hsl(1deg, 60%, 62%);
}
@media (min-width: 768px) {
  .offer {
    padding: 17px 5px;
    font-size: 14px;
  }
}
.offer__text {
  max-width: 60%;
  line-height: 1.5;
  margin: auto;
}
@media (min-width: 768px) {
  .offer__text {
    max-width: 100%;
  }
}
.offer__text--bold {
  color: #fff;
}

.form {
  margin-top: 25px;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 20px;
  box-shadow: 0 8px 2px hsl(1deg, 60%, 62%);
  justify-content: center;
}
@media (min-width: 768px) {
  .form {
    padding: 30px;
  }
}
.form__item {
  padding: 15px;
  border: 0.8px solid hsl(0deg, 0%, 90%);
  border-radius: 5px;
  color: hsl(249deg, 10%, 26%);
  width: 100%;
  position: relative;
}
.form__item::placeholder {
  font-weight: 700;
  color: hsl(249deg, 10%, 26%);
  opacity: 0.9;
}
.form__item:focus-visible {
  outline: none;
}
.form__item:focus-visible::placeholder {
  opacity: 0.5;
}
.form__submit {
  padding: 15px;
  text-transform: uppercase;
  background: linear-gradient(to bottom, hsl(154deg, 59%, 51%) 0%, hsl(154deg, 59%, 51%) 100%);
  border: none;
  border-radius: 8px;
  color: #fff;
}

.error {
  border: 2px solid hsl(0deg, 100%, 74%);
}
.error ~ .image__svg--success {
  color: hsl(0deg, 100%, 74%);
}

.success {
  border: 1px solid hsl(248deg, 32%, 49%);
  caret-color: hsl(248deg, 32%, 49%);
}
.success ~ .image__svg {
  opacity: 0;
}
.success ~ .image__svg--success {
  opacity: 1;
}

.eye {
  bottom: 10px;
  color: hsl(248deg, 32%, 49%);
  cursor: pointer;
  display: block;
  position: absolute;
  right: 22px;
  width: 20px;
}

.hide {
  display: none;
}

.form__error {
  margin-top: 5px;
  color: hsl(0deg, 100%, 74%);
  text-align: right;
  font-size: 12px;
}

.error-div {
  position: relative;
}

.image__svg {
  position: absolute;
  top: 13.5px;
  right: 25px;
  width: 20px;
  opacity: 0;
}

.terms-and-services {
  color: hsl(246deg, 25%, 77%);
  font-size: 12px;
  max-width: 250px;
  margin: auto;
}
@media (min-width: 768px) {
  .terms-and-services {
    max-width: 100%;
    font-size: 10px;
  }
}
.terms-and-services__span {
  color: hsl(0deg, 100%, 74%);
}