@use 'base/variables' as v;
@use 'base/mixins' as m;
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-image: url(../img/bg-intro-mobile.png) ,linear-gradient(v.$Red 0% ,v.$Red 100%);
    padding-block: 50px;
    padding-inline: 30px ;
    text-align: center;
    color: #fff;
    font-family: v.$font-family;

    @include m.tablet{ 
        background-image: url(../img/bg-intro-desktop.png)
        ,linear-gradient(v.$Red 0% ,v.$Red 100%);
     }
}

.main{
    @include m.tablet{ 
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 auto;
        gap: 20px;
        max-width: 75vw;
     }
}
.denied{
    cursor:not-allowed;
    opacity: .5;
}
.accepted{
    cursor:pointer;
    opacity: 1;
}

.description{

    @include m.tablet{ 
        margin-top: 60px;
        text-align: left;
        padding: 30px;
        width: 50%;
     }
   
    &__heading{
        font-size: 20px;
        max-width: 60%;
        margin: auto;
        letter-spacing: 1px;

        @include m.tablet{ 
            font-size: 40px;
            max-width: 100%;
            line-height: 1.2;
            margin: 0;
         }
    }

    &__text{
        margin: 20px auto;
        font-size: 12px;
        line-height: 1.5;
        max-width: 75%;

        @include m.tablet{ 
            margin:10px 20;
            font-size: 14px;
            line-height: 1.5;
            max-width: 100%;
         }
    }
}
.content{
    max-width: 450px;
    margin: 0 auto;
}

.offer{
    color: v.$Grayish-Blue;
    margin-top: 40px;
    background-color: v.$Blue ;
    padding: 10px 5px ;
    font-size: 12px;
    border-radius: 10px;
    box-shadow: 0 8px 2px  hsl(1, 60%, 62%);
   
    @include m.tablet{ 
         padding:17px 5px; 
         font-size: 14px;
     }

    &__text{
        max-width: 60%;
        line-height: 1.5;
        margin: auto;
        @include m.tablet{ 
            max-width:100%;
         }

        &--bold{
            color: #fff;
        }
    }
}

.form{
    margin-top: 25px;
    background-color: #fff;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 20px;
    box-shadow: 0 8px 2px hsl(1, 60%, 62%);
 justify-content: center;
    @include m.tablet{ 
         padding: 30px;
     }
    &__item{
        padding: 15px;
        border: .8px solid hsl(0, 0%, 90%);
        border-radius: 5px;
        color: v.$Dark-Blue;
        width: 100%;
        position: relative;

        &::placeholder{
            font-weight: 700;
            color:v.$Dark-Blue;
            opacity: .9;
        }
        &:focus-visible{
            outline: none;
            &::placeholder{
                opacity: .5;
            }
        }
    }

    &__submit{
        padding: 15px;
        text-transform: uppercase;
        background:linear-gradient(to bottom ,v.$Green 0% , v.$Green 100%);
        border: none;
        border-radius: 8px;
        color: #fff;
    }

}
.error {
    border: 2px solid v.$Red;

    & ~ .image__svg{
        
        &--success{
           color: hsl(0, 100%, 74%);
        }  
   }
}
.success {
    border: 1px solid v.$Blue;
    caret-color: v.$Blue;
    & ~ .image__svg{
        opacity: 0; 
        &--success{
            opacity: 1;
           }  
   }
   
}
.eye{
    bottom: 10px;
    color: hsl(248deg 32% 49%);
    cursor: pointer;
    display: block;
    position: absolute;
    right: 22px;
    width: 20px;
}
.hide{
    display: none;
}
.form__error{
    margin-top: 5px;
    color: v.$Red;
    text-align: right;
    font-size: 12px;
}
.error-div{
    position: relative;
}
.image__svg{
    position: absolute;
    top: calc((47px/2) - 10px);
    right: 25px;
    width: 20px;
    opacity: 0; 
}
.terms-and-services{
    color: v.$Grayish-Blue;
    font-size: 12px;
    max-width: 250px;
    margin: auto;
    @include m.tablet{ 
        max-width: 100%;
        font-size: 10px;
     }

    &__span{
        color: v.$Red;
    }
}
